import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  formatDate,
  moment,
  getTglSystem,
  ToastNotification,
} from "../../../components/helpers/library";
import HeadLaporanAuthorization from "./HeadLaporanTransaksi.jsx";
import { getData2 } from "../../../config/axios";

class LaporanOtorisasi extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      export: false,
      dataAuthorization: [],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    getData2(
      `authorization/reports?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}`
    )
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            dataAuthorization: [],
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Laporan Otorisasi Tersedia");
          this.setState({
            dataAuthorization: res.data,
            isLoading: false,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Otorisasi</li>
        </ol>
        <h1 className="page-header">Laporan Otorisasi </h1>
        <Panel>
          <PanelHeader>Laporan Otorisasi</PanelHeader>
          <br />
          {/* Laporan Otorisasi */}
          <div className="container">
            <HeadLaporanAuthorization
              dataAuthorization={this.state.dataAuthorization}
              onSubmit={(data) => this.handleSubmit(data)}
              export={this.state.export}
              isLoading={this.state.isLoading}
            />
          </div>
          <br />
          {/* End Tambah Laporan Otorisasi  */}
        </Panel>
      </div>
    );
  }
}

LaporanOtorisasi = reduxForm({
  form: "LaporanOtorisasi",
  enableReinitialize: true,
})(LaporanOtorisasi);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(LaporanOtorisasi);
